/* Reset styles */
* {
	margin: 0;
	padding: 0;
}

:root {
	--color-secondary: #c13232;
	--color-ternary: #2c2c2c;
	--color-white: #f4fefd;
	--color-black: #000000;
}
body {
	background: #1c1c1c;
	color: var(--color-white);
}


.main-page {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100%;
    min-height: 90vh;
	justify-content: center;
}
.collapsible {
	margin-top: 20px;
	background-color: #eee;
	color: #444;
	cursor: pointer;
	padding: 18px;
	border: none;
	text-align: left;
	outline: none;
	font-size: 15px;
}
.active,
.collapsible:hover {
	background-color: #ccc;
}
.collapsible-div {
	padding: 20px 20px;
	display: none;
	background-color: #f1f1f1;
	color: #444;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
}

.paragraph{
    margin-top: 20px;
    
}

.app-logo {
	max-width: 70%;
	max-height: 70%;
}


h1 {
	font-size: 3rem;
}

::-webkit-scrollbar {
	width: 10px; /* Width of the scrollbar */
}

::-webkit-scrollbar-thumb {
	background: var(--color-secondary); /* Color of the scrollbar handle */
	border-radius: 5px; /* Rounded corners of the handle */
}

::-webkit-scrollbar-track {
	background: "transparent"; /* Color of the scrollbar track */
}

.container {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 20px;
	text-align: center;
}




.content {
	display: flex;
    flex-direction: column;
    align-items: center;
}


footer {
	background-color: #2c2c2c;
	color: #f4fefd;
	height: 10vh;
	text-align: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
}


.previousslide {
	width: 30%;
	height: 30%;
	max-width: 800px;
}
.previousslide img {
	outline: 2px solid var(--color-secondary);
	border-radius: 10px;
	transition: transform 0.3s ease;
	filter: brightness(50%);
}
.nextslide {
	width: 30%;
	height: 30%;
	max-width: 800px;
}
.nextslide img {
	outline: 2px solid var(--color-secondary);
	border-radius: 10px;
	transition: transform 0.3s ease;
	filter: brightness(50%);
}

.currentslide {
	width: 40%;
	margin-right: 10px;
	margin-left: 10px;
	max-width: 900px;
}
.currentslide img {
	outline: 2px solid var(--color-secondary);
	border-radius: 10px;
	transition: transform 0.3s ease;
}
.carousel-subtext {
	margin-top: 10px;
	max-width: 50%;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.slides {
	display: flex;
	justify-content: center;
	align-items: center;
}

.carousel {
	width: 100%;
	margin-top: 10px;
	display: flex;
	flex-direction: column;
	align-content: center;
	align-items: center;
	justify-content: center;
}

.carousel-slide {
	display: none;
	text-align: center;
}
.carousel-slide .slides {
	display: flex;
	justify-content: center;
	align-items: center;
}

.carousel-dots {
	display: flex;
	justify-content: center;
	margin-top: 10px;
}

.carousel-dot {
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background-color: rgba(252, 252, 252);
	margin: 0 5px;
	cursor: pointer;
	border: 1px solid black;
}

.carousel-dot.active {
	background-color: rgba(0, 0, 0);
	border: 1px solid white;
}

.nextslide:hover,
.previousslide:hover {
	transform: translateY(-5px);
	cursor: pointer;
}

@media (max-width: 1500px) {
	.container {
		display: flex;
		flex-direction: column;
	}

}
@media (max-width: 1800px) {
	.nextslide:hover {
		transform: translateY(5px);
		cursor: pointer;
	}
	.currentslide {
		margin-right: 0;
		margin-left: 0;
		margin-top: 10px;
		margin-bottom: 10px;
	}
	.currentslide {
		width: 100%;
		
	}
	.nextslide {
		width: 80%;
		
	}
	.previousslide {
		width: 80%;
		
	}
	.carousel{
		width: 80%;
	}
	.slides {
		display: flex;
		flex-direction: column;
		justify-content: center;
		max-height: 50%;
	}
}

@media (max-width: 900px) {
	.currentslide {
		width: 100%;
	}
	.nextslide {
		width: 80%;
	}
	.previousslide {
		width: 80%;
	}
}
